<template>
  <div class="app-wrapper" :id="app.siteInfo.yinhe?'yinhe':''">
        <NewHeader :user="user" :product="product" :app="app" />

        <AppMain />
        <Footer />
        
        <GlobalDialog :user="user" :app="app" />

        <CustomerHelper :user="user" :app="app" :static="false" />


        
  </div>
</template>

<script>
import AppMain from './components/AppMain'
// import Header from './components/Header'
import NewHeader from './components/Header/NewHeader'
import Footer from './components/Footer'
import GlobalDialog from './components/GlobalDialog'
import CustomerHelper from './components/CustomerHelper'

import { mapGetters } from 'vuex'

export default {
  name: 'Layout',
  data() {
      return {
          loginForm: {
              name: ''
          },
      }
  },
  components: {
    // Header,
    NewHeader,
    Footer,
    AppMain,
    GlobalDialog,
    CustomerHelper,
  },
  computed: mapGetters({
    user: 'user',
    product: 'product',
    app: 'app',
  }),
  mounted() {
    //   document.title = this.app.siteInfo.name || '';
    if(this.user.roles.indexOf('user') > -1) {
      const sockName = 'tick_' + this.user.userInfo.id;
      window.socket.off(sockName);
      window.socket.on(sockName, msg => {
          this.$store.dispatch('tick').then(() => {
            this.$alert('您已在其他地方登录该账户', '提示', {
                confirmButtonText: '确定',
                showClose: false,
                callback: action => {
                    window.location.reload();
                }
            });
          })
      });
    }
  },
  methods: {

  }
}
</script>

