/* 图片 格式化*/



export function imgFormatter(src) {
  // return src;
  if(!src) return src;

  const host = window.location.host.replace('m.', '').replace('www.', '');
  
  let imgPrefix = '//img.' + host;

  // if(host.indexOf('sybet.vip') > -1) {
  //   imgPrefix = '//img.' + host;
  // }
  
  if(process.env.NODE_ENV !== 'production' || window.TEST_SERVER == 1) {
    // imgPrefix = '//img.lrak8e.club';
    imgPrefix = 'https://assets.systaitc.com';
  }else {
    imgPrefix = 'https://assets.systaitc.com';
  }

  return imgPrefix + (src[0] == '/' ? '' : '/') + src;
}


export function cardNumberFilter(number) {
  if(!number) return '';

  const arr = String(number).split('');
  var result = [];

  for(var i=0, len=arr.length; i<len; i+=4){
      result.push(arr.slice(i,i+4));
  }

  return result.map(s => s.join('')).join(' ');
}


export function moneyToFixed(number) {
  if(isNaN(Number(number))) {
    return number;
  }

  return Number(number).toFixed(2)
}
