<template>
    <header id="header" height="auto">
        
        <div class="container">
            <InfoBar :user="user" :product="product" :app="app" />
        </div>

        <Nav :user="user" :product="product" :app="app" />
        
    </header>
</template>

<script>
import { mapGetters } from 'vuex'
import InfoBar from './InfoBar'
import Nav from './Nav'
import { 
    usernameValidator,
    passwordValidator,
} from '@/utils/validator'
import { imgFormatter } from '@/utils/index'
import { 
    getAnnounceList,
} from '@/api/common'

let timer = null;

export default {
    name: 'NewHeader',
    props: ['user', 'product', 'app'],
    components: {
        InfoBar,
        Nav
    },
    watch: {
        
    },
    data() {
        return {
        }
    },

    computed: {

        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && checkNavIsShow(item.id);
            });

            return list;
        },

    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
           if(this.user.roles.indexOf('user') != -1) {
                this.reloadBalance();
            }
        },

        reloadBalance() {
            const run = () => {
                if(this.user.roles.indexOf('user') > -1) {
                    const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
                    this.$store.dispatch('productBalance', { product_ids })
                }
            }

            run();

            clearInterval(this.timer);
            this.timer = setInterval(run, 20000);
        },
    }
}
</script>

