import Vue from 'vue'
import Router from 'vue-router'

/* Layout */
import Layout from '@/views/layout/Layout'

Vue.use(Router)

export const constantRouterMap = [
  {
    path: '/',
    component: Layout,
    hidden: true,
    children: [
      {
        path: '',
        name: 'home-index',
        component: () => import('@/views/home/Index')
      },
    ],
  },
  {
    path: '/product',
    component: Layout,
    hidden: true,
    children: [
      {
        path: ':productId',
        name: 'product-page',
        component: () => import('@/views/product/Index')
      },
    ],
  },
  { 
    path: '/p',
    component: () => import('@/views/product/Url'),
  },
  {
    path: '/about',
    component: Layout,
    hidden: true,
    children: [
      {
        path: ':page?',
        name: 'about-page',
        component: () => import('@/views/about/Index')
      },
    ],
  },
  {
    path: '/vip',
    component: Layout,
    hidden: true,
    children: [
      {
        path: '',
        name: 'about-vip',
        component: () => import('@/views/about/Vip')
      },
    ],
  },
  

  {
    path: '/activity',
    component: Layout,
    hidden: true,
    name: 'activity',
    children: [
      {
        path: '',
        name: 'activity-index',
        component: () => import('@/views/activity/Activity'),
      },
      {
        path: ':name',
        name: 'activity-detail',
        component: () => import('@/views/activity/Detail'),
      },
    ],
  },
];

export default new Router({
    mode: 'history',
    routes: constantRouterMap,
})

