import request from '@/utils/request'

export function init() {
  return request.get('/common/init');
}

export function getBannerList() {
  return request.get('/common/bannerList');
}

export function getAnnounceList() {
  return request.get('/common/announceList');
}

export function getBannerAndAnnounceList() {
  return request.get('/common/getBannerAndAnnounceList');
}

export function getAddr() {
  return request.get('/common/getAddr');
}

export function getPaychannel(data) {
  return request.get('/common/getPaychannel', data);
}