<script>
import { mapGetters } from 'vuex'
import { 
    usernameValidator,
    passwordValidator,
} from '@/utils/validator'
import { 
    getAnnounceList,
} from '@/api/common'
import { imgFormatter } from '@/utils/index';
import MoneyDropdown from '@/views/layout/components/MoneyDropdown';


export default {
    name: 'NewHeader',
    props: ['user', 'product', 'app'],
    components: {
        MoneyDropdown,
    },
    data() {
        return {
            tipDialogVisible: false,
            loginForm: {
                username: '',
                password: ''
            },

            loginFormRules: {
                username: { require: true, validator: usernameValidator, trigger: 'blur'},
                password: passwordValidator,
            },

            setFavorDialogVisible: false,

            loading: false,
            vipInfo: null,
        }
    },

    computed: {
        
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            if(this.user.roles.indexOf('user') > -1) {
                
                this.$store.dispatch('getConfig', 'vip').then(list => {
    				this.vipInfo = list;
    			});   
            }
        },

        btnRegister() {
            this.$store.dispatch('openRegisterDialog');
            // this.tipDialogVisible = true;
        },
        
        tryPlay() {
            this.$store.dispatch('tryPlay');
        },

        tipDialoghandleClose(done) {
            this.$store.dispatch('openRegisterDialog');

            if(done) {
                done();
            }else {
                this.tipDialogVisible = false;
            }
        },

        login() {
            if(this.loading) return ;

            this.loginForm.username = this.loginForm.username.trim();

            this.$refs.loginForm.validate((valid, msg) => {
                if(valid) {
                    this.loading = true;
                    this.$store.dispatch('loginByUsername', {
                        username: this.loginForm.username, 
                        password: this.loginForm.password
                    }).then((json) => {
                        if(json.code == 200) {
                            this.$message.success('登录成功');
                        }else {
                            this.loading = false;
                        }
                        // this.$router.push({ path: this.redirect || '/' })
                    })
                }else {
                    if(msg.username && msg.username[0]) {
                        this.$message.error(msg.username[0].message);
                    }else if(msg.password && msg.password[0]) {
                        this.$message.error(msg.password[0].message);
                    }
                }
            });
        },

        logout() {
            this.$store.dispatch('logout').then(() => {
               this.$router.push({ path: this.redirect || '/' })
            })
        },

        imgFormatter(src) {
            return imgFormatter(src)
        },
        
    }
}
</script>

