<template>
    <div>

        <el-dialog :title="(app.loginDialogShow ? '登录' : '注册') + '账号'" @close="authDialogclose" :visible="app.loginDialogShow || app.registerDialogShow" width="450px" class="auth_dialog">
            <el-form ref="loginForm" :model="loginForm" :rules="loginFormRules" v-if="app.loginDialogShow">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="loginForm.username" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input type="password" v-model="loginForm.password" autocomplete="off"></el-input>
                </el-form-item>                                 
                <div class="tips">
                    <a href="javascript:" @click="toRegister">没有账号？去注册</a>
                </div>
            </el-form>

            <el-form ref="registerForm" :model="registerForm" :rules="registerFormRules" v-if="app.registerDialogShow">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="registerForm.username" clearable placeholder="请输入账号，长度不能小于6位"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input type="password" clearable v-model="registerForm.password" autocomplete="off" placeholder="请输入密码，长度不能小于6位"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="rePassword">
                    <el-input type="password" clearable v-model="registerForm.rePassword" autocomplete="off" placeholder="请再次输入密码，长度不能小于6位"></el-input>
                </el-form-item>
                <el-form-item label="邀请码" prop="code" v-if="!user.domain.invite_code && !localCode">
                    <el-input v-model="registerForm.code" @keyup.enter.native="authDialogSubmit" clearable placeholder="请输入邀请码"></el-input>
                </el-form-item>
                <div class="tips">
                    <a href="javascript:" @click="toLogin">已有账号？去登录</a>
                </div>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="authDialogclose">关 闭</el-button>
                <el-button type="primary" :loading="loginRegisterLoading" @click="authDialogSubmit">提 交</el-button>
            </div>
        </el-dialog>


        <el-dialog
            title="绑定银行卡"
            :visible.sync="app.addBankCardShow"
            width="30%"
        >
            <div class="new_card">
                <el-form ref="bankForm" :model="bank.form" :rules="bank.formRules" label-width="100px" label-position="top">
                    <el-form-item label="开户银行" prop="open_account_bank">
                        <el-select v-model="bank.form.open_account_bank" placeholder="请选择">
                            <el-option
                            v-for="name in bank.bank_list"
                            :key="name"
                            :label="name"
                            :value="name">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="开户城市" prop="areaSelect">
                        <el-cascader
                            change-on-select
                            expand-trigger="hover"
                            :options="app.areaOptions"
                            v-model="bank.form.areaSelect"
                        >
                        </el-cascader>
                    </el-form-item>
                    <el-form-item label="银行卡号" prop="bank_account">
                        <el-input v-model="bank.form.bank_account" placeholder="请输入银行卡号"></el-input>
                    </el-form-item>

                    <el-form-item label="开户姓名" prop="name">
                        <el-input v-model="bank.form.name" placeholder="请输入姓名"></el-input>
                    </el-form-item>

                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="$store.dispatch('closeAddBankCardDialog')">取 消</el-button>
                <el-button type="primary" @click="addBankCardSubmit" :loading="bank.loading">提 交</el-button>
            </span>
        </el-dialog>


        <el-dialog title="活动详情" class="detail_dialog" :visible.sync="app.scActivityDialogVisible">
            <el-form label-width="100px" label-position="left">
                <el-form-item label="活动名称：">
                    首次存款有奖，新人即送888.88元彩金
                </el-form-item>
                <el-form-item label="活动时间：">
                    即日起
                </el-form-item>
                <el-form-item label="申请方式：">
                    前往充值页面，勾选首冲活动即可
                    <!-- 首次存款完成后，联系客服申请活动奖励 -->
                    <a href="javascript:;" @click="toCharge">前往充值</a>
                </el-form-item>
                <el-form-item label="赠送金额：">
                    <el-table
                        size="small"
                        :data="[
                            { amount: 200, gold: 18.88, check: 5 + '倍'},
                            { amount: 500, gold: 58.88, check: 5 + '倍'},
                            { amount: 1000, gold: 108.88, check: 5 + '倍'},
                            { amount: 3000, gold: 328.88, check: 5 + '倍'},
                            { amount: 5000, gold: 568.88, check: 5 + '倍'},
                            { amount: 10000, gold: 888.88, check: 5 + '倍'},
                        ]"
                        border
                        style="width: 80%"
                    >
                        <el-table-column
                            prop="amount"
                            label="充值"
                        >
                        </el-table-column>
                        <el-table-column
                            prop="gold"
                            label="赠送"
                        >
                        </el-table-column>
                        <el-table-column
                            prop="check"
                            label="稽核"
                        >
                        </el-table-column>
                    </el-table>
                </el-form-item>
                <el-form-item label="活动规则：">
                    <ul>
                        <li>1. 每位会员终身仅能享受1次首存优惠活动，完成首次存款并达到金额要求，请在1天内申请奖励，逾期视为放弃奖励。</li>
                        <li>2. 第一次存款若金额不符，则失去首存送的优惠机会。</li>
                        <li>3．若符合活动条件，但无法申请（申请按钮不可点击），则说明您的会员账号可能存在风险，暂时拒绝您参与该优惠。（例：多个相同银行卡的账号、IP下注册多个账号、手机号码错误等原因都可能造成您无法参与首存优惠）</li>
                        <li>4．活动存款方式包含网银充值，支付宝扫码，微信扫码。</li>
                        <li>5．该活动与每日首存、存就送活动不可同时享受，此存款订单号只能用于一种活动的申请。</li>
                        <li>6．参与该优惠活动，即表示您同意《优惠条款》。</li>
                    </ul>
                </el-form-item>
                <el-form-item label="优惠条款：">
                    <ul>
                        <li>1．所有优惠以人民币为结算金额，以东8区时间为准。</li>
                        <li>2．邮箱地址、电话号码、银行卡号相同的会员，视为同一位会员，{{ app.siteInfo.name }}保留收回或取消申请优惠金的权利</li>
                        <li>3．{{ app.siteInfo.name }}的所有优惠特为玩家而设，如发现任何团体或个人，以不诚实方式套取红利或任何威胁、滥用公司优惠等行为，公司保留冻结、取消该团体或个人账户及账户结余的权利。</li>
                        <li>4．若会员对活动有争议时，为确保双方利益，杜绝身份盗用行为，{{ app.siteInfo.name }}有权要求会员向我们提供充足有效的文件，用以确认是否享有该优惠的资质。</li>
                        <li>5．本活动最终解释权归属{{ app.siteInfo.name }}所有，并保留修改以上条款的最终权利。</li>
                    </ul>
                </el-form-item>
                <el-form-item>
                    <el-button type="success" @click="toCharge">前往充值</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { 
    usernameValidator,
    passwordValidator,
    customCodeValidator,
} from '@/utils/validator'

export default {
  name: 'Global-Dialog',
  props: ['user', 'app'],
  data() {
        var validateArea = (rule, value, callback) => {
            if (!value || value.length != 2) {
                callback(new Error('请选择开户城市'));
            } else {
                callback();
            }
        };

        var validateRePassword = (rule, value, callback) => {
            if(value == '') {
                callback(new Error('不能为空'));
            }
            else if (value != this.registerForm.password) {
                callback(new Error('密码不一致'));
            } else {
                callback();
            }
        }

        return {
            loginForm: {
                username: '',
                password: '',
            },

            loginFormRules: {
                username: { require: true, validator: usernameValidator, trigger: 'blur'},
                password: passwordValidator,
            },

            registerForm: {
                username: '',
                password: '',
                rePassword: '',
                code: ''
            },

            registerFormRules: {
                username: { require: true, validator: usernameValidator, trigger: 'blur'},
                password: passwordValidator,
                rePassword: { require: true, validator: validateRePassword, trigger: 'blur'},
                code: customCodeValidator,
            },

            loginRegisterLoading: false,

            bank: {
                bank_list: [
                    '工商银行', '建设银行', '农业银行', '民生银行', '招商银行', '交通银行', '中国银行', '邮政储蓄', '中信银行', '兴业银行', '华夏银行', '浦发银行', '广发银行', '平安银行', '光大银行', '农商银行', '台州银行'
                ],
                form: {
                    open_account_bank: '',
                    areaSelect: [],
                    bank_account: '',
                    name: '',
                },
                formRules: {
                    open_account_bank: [{ required: true, message: '请选择开户银行', trigger: 'change' }],
                    areaSelect: [{ required: true, validator: validateArea, trigger: 'change' }],
                    bank_account: [{ required: true, message: '请选择银行卡号', trigger: 'blur' }],
                    name: [{ required: true, message: '请选择开户姓名', trigger: 'blur' }],
                },
                loading: false,
            },
        }
  },
  components: {

  },

  computed: {
      localCode() {
          return window.localStorage.getItem('code');
      },
  },

  mounted() {

  },
  methods: {
    toRegister() {
        this.$store.dispatch('closeLoginDialog');
        this.$store.dispatch('openRegisterDialog');
    },

    toLogin() {
        this.$store.dispatch('closeRegisterDialog');
        this.$store.dispatch('openLoginDialog');
    },

    authDialogclose() {
        this.$store.dispatch('closeLoginDialog');
        this.$store.dispatch('closeRegisterDialog');
    },

    authDialogSubmit() {
        if(this.app.loginDialogShow) {
            if(this.loginRegisterLoading) return ;

            this.$refs.loginForm.validate((valid) => {
                if(valid) {
                    this.loginRegisterLoading = true;
                    this.$store.dispatch('loginByUsername', {
                        username: this.loginForm.username, 
                        password: this.loginForm.password
                    }).then((json) => {
                        this.loginRegisterLoading = false;
                        // if(json.code == 200) {
                        //     this.$message({
                        //         message: '登录成功',
                        //         type: 'success'
                        //     });
                        // }
                    })
                }
            });
            
        }else if(this.app.registerDialogShow) {
            if(this.loginRegisterLoading) return ;

            if(this.user.domain.invite_code) {
                this.registerForm.code = this.user.domain.invite_code;
            }

            if(this.localCode) {
                this.registerForm.code = this.localCode;
            }

            this.registerForm.username = this.registerForm.username.trim();

            this.$refs.registerForm.validate((valid) => {
                if (valid) {

                    var data = {
                        username: this.registerForm.username,
                        password: this.registerForm.password,
                        code: this.registerForm.code,
                    };

                    if(this.registerForm.rePassword != this.registerForm.password) {
                        this.$message.error('两次密码不一致');
                        return;
                    }

                    // this.loginRegisterLoading = true;
                    this.$store.dispatch('userRegister', data).then((json) => {
                        this.loginRegisterLoading = false;
                        if(json.code == 200) {
                            // this.$message({
                            //     message: '注册成功',
                            //     type: 'success'
                            // });

                            // this.$router.push({ path: '/' })
                        }
                    })
                } else {
                    console.log('提交错误');
                    return false;
                }
            });
        }
    },

    addBankCardSubmit() {
        this.$refs.bankForm.validate((valid) => {
            if (valid) {
                var data = {
                    name: this.bank.form.name,
                    open_account_bank: this.bank.form.open_account_bank,
                    bank_account: this.bank.form.bank_account,
                    open_account_province: this.bank.form.areaSelect[0] + '-' + this.bank.form.areaSelect[1],
                    open_account_address: '',
                    id_card: '',
                }

                if(this.bank.loading) return ;
                this.bank.loading = true;
                this.$store.dispatch('addBankcard', data).then(json => {
                    this.bank.loading = false;

                    if (json.code == 200) {
                        this.$message.success('添加成功');
                        this.bank.form = {
                            open_account_bank: '',
                            areaSelect: [],
                            bank_account: '',
                            name: '',
                        };
                        this.$store.dispatch('bankcardList');
                        this.$store.dispatch('closeAddBankCardDialog')
                    } else {
                        this.$message.error(json.info);
                    }
                })
            }
        })
    },

    toCharge() {
        this.$store.dispatch('closeScActivityDialog');
        this.$router.push('/user/charge');
    }
  }
}
</script>

