<template>
    <div class="nav">
        <div class="container nav_wrap">
            <router-link class="navbar-brand" to="/">
                <img v-if="app.siteInfo.logo" :src="app.siteInfo.logo | imgFormatter" />
            </router-link>

            <ul class="nav_list">
                <template v-for="(nav, index) in navListFilter">
                    <li :style="navItemStyle" :class="{ hover: currentNav && currentNav.id == nav.id }" @mouseenter="navMouseEnter(nav)" @mouseleave="navMouseLeave">
                        <a class="nav_link" href="javascript:;">
                            {{ nav.name }}
                        </a>

                        <!-- <el-dropdown class="nav_link">
                            <span class="el-dropdown-link">
                                {{ nav.name }}
                            </span>
                            <el-dropdown-menu class="nav_dropdown sub_nav" slot="dropdown">
                                <el-dropdown-item :key="product.id" v-for="product in nav.list">
                                    <router-link to="/user/charge" class="username">
                                        <img :src="product.icon | imgFormatter" height="14" /> {{ product.name }}
                                    </router-link>
                                </el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown> -->

                    </li>

                    <li class="topMenu_line"></li>
                </template>
                <li :style="navItemStyle" class="activity other_nav">
                    <router-link class="nav_link" to="/activity"><i class="fas fa-fire-alt"></i>&nbsp; 优惠活动</router-link>
                </li>
                <li class="topMenu_line"></li>
                <li :style="navItemStyle" class="other_nav">
                    <router-link class="nav_link" to="/about/app"><i class="fas fa-mobile-alt"></i> APP下载</router-link>
                </li>
            </ul>
            <div class="clearfix"></div>
        </div>

        <transition name="el-zoom-in-top">
            <div class="game_list_wrap" v-show="navGameListIsShow" >
                <div :class="'container game_list nav_' + (currentNav ? currentNav.id : '')" @mouseenter="gameListMouseEnter" @mouseleave="gameListMouseLeave">
                    <ul v-if="gameList.length > 0">
                        <li
                            v-for="(item, index) in gameList" 
                            :key="item.id"
                            :style="{ 
                                width: (getNavWidth(gameList, item)) + '%', backgroundImage: item.bg ? 'url('+ (item.bg) +')' : 'none'
                            }"
                            :class="{ fixed: item.is_fix == 'Y', hightOdds: app.hightOddsId == item.id }"
                            >
                            <router-link :to="getProductLink(item.id)" @click.native="linkClick(item.id)" v-if="item.is_fix == 'N'" :target="app.hightOddsId == item.id ? '_self' : '_blank'">
                                <span class="info">
                                    <img :src="item.icon | imgFormatter" />
                                    <p>{{ item.name }}</p>
                                </span>
                            </router-link>

                            <a v-else href="javascript:;">
                                <span class="info">
                                    <img :src="item.icon | imgFormatter" />
                                    <p>{{ item.name }}</p>
                                </span>
                            </a>
                        </li>

                        <template v-if="gameList[0].nav_id != 8 && gameList[0].nav_id != 7 && gameList.length < 4">

                            <li
                                v-for="(item, index) in new Array(4 - gameList.length)" 
                                :key="index"
                                :style="{ 
                                    width: '25%', backgroundImage: 'url('+ comingBg +')'
                                }"
                            >
                            </li>

                        </template>


                    </ul>
                </div>
            </div>
        </transition>

        <el-dialog
            title="提示"
            :visible.sync="activityDialogVisible"
            width="30%"
        >
            <span>更多优惠活动，敬请期待！</span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="activityDialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { imgFormatter } from '@/utils/index'
let navTimer = null;
let subNavTimer = null;


export default {
    name: 'Nav',
    props: ['user', 'product', 'app'],
    components: {
    },
    watch: {
        
    },
    data() {
        const bgList = {
            // 彩票
            4: [
                '/images/2020/03/13/f2d714a2-0859-447f-9e02-d374c985e80a.jpg',
                '/images/2020/03/13/cf93c713-e70b-4587-a143-26c5ee4b5d49.jpg',
                '/images/2020/03/13/9b0ca9f3-6f5f-4ae2-a19e-c24be1b57cc7.jpg',
                
                // '/images/2019/06/26/394052aa-2f68-4f35-b768-f6114edbedc3.jpg',
                // '/images/2019/06/26/64b797b0-5107-4e44-85fd-d991417bac91.jpg',
                // '/images/2019/07/11/87c81aae-47e5-41de-bff3-a380df97e95e.jpg',
                // '/images/2019/08/01/5295114f-f5d3-4bc6-bb11-484ba2abf01c.jpg',
            ],
            // 电子
            2: [
                '/images/2020/03/13/fce8aef1-562b-481c-99ac-860e23925a91.jpg',
                '/images/2020/03/13/5560b5df-ff1b-40dd-ba7f-653398cf092c.jpg',
                '/images/2020/03/13/71eff139-f303-47fc-87f9-47c3b72a9225.jpg',
                '/images/2020/03/13/146127d2-de93-4244-adf3-1d33f9cf5726.jpg',
                
                // '/images/2019/06/26/18135da5-dcc4-452a-b9a5-5dc68ac728ef.jpg',
                // '/images/2019/06/26/23d8525e-b2cf-4d10-8ba9-67cab43688dc.jpg',
                // '/images/2019/09/24/85ce973b-d6e0-4e79-adf5-78a202ec06f9.jpg',
                // '/images/2020/02/24/dbe9860a-0f8e-4b3f-be85-db100e209a4a.jpg',
                
            ],
            // 视讯
            1: [
                '/images/2020/03/13/0fe6e199-1cfc-4bb9-a455-d3a7faad07bc.jpg',
                '/images/2020/03/13/52e97a1f-c61a-44fe-bcdf-be226c5e333a.jpg',
                '/images/2020/03/13/bbde17b4-8640-400a-a55c-44d35b1616cc.jpg',
                '/images/2020/03/13/6a97fc84-75a2-471e-a59f-6ed0dd939f2f.jpg',
                '/images/2020/03/13/c8d58fe8-4c4b-41b5-8c66-ae06c390825f.jpg',
                
                // '/images/2019/10/02/2cad6274-522d-4d6a-833f-bfe212c5543c.jpg',
                // '/images/2019/10/02/11c5af9d-0841-4def-a73a-e28f0f5adc09.jpg',
                // '/images/2019/10/02/413bee98-9cb2-4d3e-9567-268159cdddd5.jpg',
                // '/images/2019/10/02/d372a5b2-7ef0-4551-b576-539728ba197a.jpg',
                // '/images/2019/10/02/8e9cb1cb-103d-4471-83d4-c375a010daf1.jpg',
            ],
            // 棋牌
            3: [
                '/images/2020/03/13/2ed83a6e-49d7-4a44-8621-a24e391b741c.jpg',
                '/images/2020/03/13/1e62f380-0f80-4dfb-849d-76a083900e10.jpg',
                '/images/2020/03/13/4ea16627-2626-43df-a6cf-fd6f2a1f2734.jpg',
                
                // '/images/2019/06/26/eecd2108-0f03-432a-8203-71ab823494e9.jpg',
                // '/images/2019/06/26/e54a7494-ab20-4261-918e-6fead0bbe583.jpg',
                // '/images/2019/09/12/82da525e-82f3-4035-9762-56410b1c4828.jpg',
            ],
            // 电竞
            8: [
                '/images/2020/03/13/2f76a968-5ae0-4d9b-b78d-1ddb0b1f5989.jpg',
                '/images/2020/03/13/e454faec-ec2a-4ad5-8475-45b3e807e7a6.jpg',
                // '/images/2019/06/26/7b3a9b7b-4d0d-446f-b022-9473e4654cf9.jpg',
                // '/images/2019/06/26/b023da19-af11-4459-a6c9-380948177ba4.jpg',
            ],
            // 体育
            7: [
                '/images/2020/03/13/1d626892-37ab-479a-96f0-f8da94c8a98f.jpg',
                // '/images/2019/06/26/931ae1a0-a257-4c5c-9a72-3e6d5bc92a1b.jpg',
                // '/images/2019/06/26/19808a5f-78a0-4ea3-b970-47a325e28ba5.jpg',
                // '/images/2019/06/26/6e5c548d-a00a-437e-9de9-5590c55c1fdb.jpg',
            ],
        };

        for(let key in bgList) {
            bgList[key] = bgList[key].map(url => {
                return imgFormatter(url)
            })
        }
        
        return {
           navGameListIsShow: false,
           currentNav: null,
           activityDialogVisible: false,
           comingBg: imgFormatter('/images/2019/07/03/b6b55122-6eb4-4bb4-b293-2b56ae235d4b.jpg'),
           bgList,
        }
    },

    computed: {
        navListFilter() {
            return this.product.navList.filter(item => {
                return item.list && item.is_show == 'Y';
            })
        },

        navItemStyle() {
            return { width: (100/(this.navListFilter.length + 2) - .1) + '%'}
        },

        gameList() {
            let list = [];

            if(this.currentNav) {
                list = this.product.list.filter(item => {
                    return item.nav_id == this.currentNav.id && item.is_show == 'Y';
                })

                const navBgList = this.bgList[this.currentNav.id];

                if(this.currentNav.id == 2) {
                    const buyuBg = navBgList[0];
                    const kgBg = navBgList[1];
                    const saBg = navBgList[2];
                    const jdbBg = navBgList[3];

                    list.forEach(item => {
                        if(item.id == 22) {
                            item.bg = buyuBg;
                        }else if(item.id == 10) {
                            item.bg = kgBg;
                        }else if(item.id == 18) {
                            item.bg = saBg;
                        }else if(item.id == 31) {
                            item.bg = jdbBg;
                        }
                    });
                }else {
                    list.forEach((item, index) => {
                        if(navBgList[index]) {
                            item.bg = navBgList[index];
                        }
                    });
                }
            }

            return list;
        },

    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            // this.navMouseEnter(this.navListFilter[1]);
        }, 

        navMouseEnter(nav) {
            // return ;
            clearTimeout(subNavTimer);

            // this.currentNav = null;
            // this.navGameListIsShow = false;

            this.$nextTick(() => {
                navTimer = setTimeout(() => {
                    this.currentNav = nav;
                    this.navGameListIsShow = true;
                }, 80);
            });
        }, 

        navMouseLeave() {
            clearTimeout(navTimer);
            clearTimeout(subNavTimer);
            subNavTimer = setTimeout(() => {
                this.currentNav = null;
                this.navGameListIsShow = false;
            }, 100);
        },

        gameListMouseEnter() {
            clearTimeout(subNavTimer);
        },

        gameListMouseLeave() {
            clearTimeout(subNavTimer);
            subNavTimer = setTimeout(() => {
                this.currentNav = null;
                this.navGameListIsShow = false;
            }, 100);
        },

        getProductLink(id) {
            let link = '/p?id=';

            if(id == 10 || id == 18) {
                link = '/product/';
            }

            link += id;

            if(this.app.hightOddsId == id) {
                link = '/activity/hightodds'
            }

            return link;
        },

        linkClick(id) {
            if(this.app.hightOddsId == id) {
                clearTimeout(subNavTimer);
                this.currentNav = null;
                this.navGameListIsShow = false;
            }
        },
        
        getNavWidth(gameList, gameItem) {
            if(gameList.length > 4) {
                return 100/gameList.length;
            }
            
            // 电竞
            if(gameItem.nav_id == 8) {
                return 50;
            }else if(gameItem.nav_id == 7) {
                return 100;
            }
            
            
            return 25;
        }
        
        

    }
}
</script>

