import router from './router'
import store from './store'
import {
	Message
} from 'element-ui'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import { init as requestInit } from './api/common' 
import { getToken, removeToken } from '@/utils/auth';
import { imgFormatter } from '@/utils/index';

NProgress.configure({
	showSpinner: true
}) // NProgress Configuration

const metaKw = document.querySelector('meta[name="keywords"]');
const metaDesc = document.querySelector('meta[name="description"]');
const iconDesc = document.querySelector('link[rel="shortcut icon"]');

router.beforeEach((to, from, next) => {
	NProgress.start() // start progress bar

	if(store.getters.app.siteInfo.name) {
		const title = store.getters.app.siteInfo.name + '--10年信誉玩家首选，亚洲第一专业娱乐品牌！(体育投注、现场游戏、电子游戏、乐透彩球)';
		if(document.title != title) {
			document.title = title;
		}
	}
	

	if(store.getters.app.siteInfo.keyword && metaKw.content != store.getters.app.siteInfo.keyword) {
		metaKw.content = store.getters.app.siteInfo.keyword;
	}

	if(store.getters.app.siteInfo.description && metaDesc.content != store.getters.app.siteInfo.description) {
		metaDesc.content = store.getters.app.siteInfo.description;
	}
	
	if(store.getters.app.siteInfo.icon) {
		const icon = imgFormatter(store.getters.app.siteInfo.icon);
		if(iconDesc.href != icon) {
			iconDesc.href = icon;
		}
	}
	

	const roles = store.getters.roles;
	if (roles.length === 0) {
		requestInit().then((res) => {
			if(res.domain == false) {
				NProgress.done();
				document.write('Error Domain');
				// window.location.href = 'http://baidu.com';
				return ;
			}else {
				store.dispatch('setDomain', res.domain.list);
			}

			// debugger;
			if(res.nav && res.nav.code == 200) {
				store.dispatch('setList', res.nav.list);
			}

			if(res.config && res.config.code == 200) {
				store.dispatch('setSiteInfo', res.config.list);
			}

			if(res.test_server == 1) {
				window.TEST_SERVER = 1;
			}

			store.dispatch('setUserInfo', res.user);
			const roles = res.user.roles;

			if(res.user.expired && getToken()) {
				store.dispatch('logout');
			}

			store.dispatch('GenerateRoutes', {
				roles
			}).then(() => { // 根据roles权限生成可访问的路由表
				router.addRoutes(store.getters.addRouters) // 动态添加可访问路由表
				next({ ...to,
					replace: true
				}) // hack方法 确保addRoutes已完成 ,set the replace: true so the navigation will not leave a history record
			})
		})
	} else {
		if (roles.indexOf('user') == -1) {
			if (to.path.indexOf('/user') != -1) {
				Message.error('请先登录账户');
				next(`/`) // 否则全部重定向到登录页
				NProgress.done();
				return;
			}
		} else {
			if (to.path == '/auth/register') {
				next(`/`) // 否则全部重定向到首页
				NProgress.done();
				return;
			}
		}

		next();

		// 没有动态改变权限的需求可直接next() 删除下方权限判断 ↓
		// if (hasPermission(store.getters.roles, to.meta.roles)) {
		// 	next()
		// 	/* 自填代码  */
		// 	const roles = store.state.user.roles // note: roles must be a array! such as: ['editor','develop']
		// 	store.dispatch('GenerateRoutes', {
		// 		roles
		// 	}).then(() => { // 根据roles权限生成可访问的路由表
		// 		router.addRoutes(store.getters.addRouters) // 动态添加可访问路由表
		// 		next({ ...to,
		// 			replace: true
		// 		}) // hack方法 确保addRoutes已完成 ,set the replace: true so the navigation will not leave a history record
		// 	})
		// } else {
		// 	next({
		// 		path: '/401',
		// 		replace: true,
		// 		query: {
		// 			noGoBack: true
		// 		}
		// 	})
		// }
	}


})

router.afterEach(() => {
	NProgress.done() // finish progress bar
})

