import request from '@/utils/request'

export function getList() {
  return request.get('/product/list');
}

export function balance(product_ids) {
  return request.get('/product/balance', { product_ids });
}

export function balanceSync(product_ids) {
  return request.get('/product/balanceSync', { product_ids });
}

export function transferallout(product_id) {
  return request.post('/product/transferallout', { product_id });
}

export function getUrl(data) {
  return request.get('/product/url', data);
}

export function getTryUrl(data) {
  return request.get('/product/tryUrl', data);
}

