export function isDev() {
	return process.env.NODE_ENV == 'development';
}

export function imgFormatter(src) {
	return imgFormatterFilter(src);
}

export function jumpToWWW() {
	const _isDev = isDev();
	if(!_isDev) {
		const href = window.location.href;
		if(href.indexOf('www.') == -1 && href.split('.').length == 2) {
			window.location.href = href.replace('http://', 'http://www.').replace('https://', 'https://www.');
			return true;
		}
	}

	return false;
}

export function jumpToMobile() {
	const href = window.location.href;
	if(href.indexOf('www.') > -1) {
		window.location.href = href.replace('www.', 'm.');
	}else {
		window.location.href = href.replace('http://', 'http://m.').replace('https://', 'https://m.');
	}
	return true;
}

export function numberToChinese(section){
     var chnNumChar = ["零","一","二","三","四","五","六","七","八","九"];
     var chnUnitChar = ["","十","百","千","万","亿","万亿","亿亿"];
     var strIns = '', chnStr = '';
     var unitPos = 0;
     var zero = true;
     while(section > 0){
         var v = section % 10;
         if(v === 0){
              if(!zero){
                   zero = true;
                   chnStr = chnNumChar[v] + chnStr;
              }
         }else{
               zero = false;
               strIns = chnNumChar[v];
               strIns += chnUnitChar[unitPos];
               chnStr = strIns + chnStr;
         }
         unitPos++;
         section = Math.floor(section / 10);
      }
      return chnStr;
}