import { getList, balance, balanceSync } from '@/api/product'

const product = {
  state: {
    navList: [],
    list: [],
    balance: [],
  },

  mutations: {
    SET_NAV_LIST: (state, list) => {
      state.navList = list;
    },
    SET_LIST: (state, list) => {
      state.list = list;
    },
    SET_BALANCE: (state, list) => {
      const newList = {};
      for(let key in list) {
        if(key) {
          newList[key] = {
            money: Number(list[key].money).toFixed(2)
          };
        }
      }

      state.balance = newList;
    },
    SET_BALANCE_BY_ID: (state, data) => {
      state.balance[data.id] = { money: data.money };
    },
  },

  actions: {
    setList({ commit }, navList) {
        const list = [];
        navList.forEach(nav => {
          if(nav.list) {
            nav.list.forEach(item => {
              list.push(item);
            })
          }
        })

        commit('SET_NAV_LIST', navList);
        commit('SET_LIST', list)
    },

    getList({ commit }, data) {
      return getList().then(json => {
          if(json.code == 200) {
            const navList = json.list;
            const list = [];

            navList.forEach(nav => {
              if(nav.list) {
                nav.list.forEach(item => {
                  list.push(item);
                })
              }
            })

            commit('SET_NAV_LIST', navList);
            commit('SET_LIST', list)
          }
      });
    },

    productBalance({ commit, rootState }, data) {
      window.socket.off('balance');
      window.socket.on('balance', (json) => {
        // console.log(Object.assign({}, json));

        if(json) {

          if (json.code == 200) {
            if(json.list.hasOwnProperty(0)) {
              commit('SET_USER_INFO_BY_KEY', { total_money: Number(json.list[0].money).toFixed(2) });
            }

            commit('SET_BALANCE', json.list)

          }

          data.callback && data.callback(json);
        }
      })

      window.socket.emit('balance', {
        product_ids: data.product_ids,
      });

      return ;
    },

    productBalanceByAjax({ commit, rootState }, data) {
      return balance(data.product_ids).then(json => {
          if(json.code == 200) {
            if(json.list.hasOwnProperty(0)) {
              commit('SET_USER_INFO_BY_KEY', { total_money: Number(json.list[0].money).toFixed(2) });
            }

            commit('SET_BALANCE', json.list)
          }

          return json;
      });
    },

    productBalanceSync({ commit }, data) {
      return balanceSync(data.product_ids).then(json => {
          if(json.code == 200) {
            for(var id in json.list) {
              commit('SET_BALANCE_BY_ID', {
                id: id,
                money: json.list[id].money
              })
            }
          }

          return json;
      });
    },

    setBalanceById({ commit }, data) {
      commit('SET_BALANCE_BY_ID', {
        id: data.id,
        money: data.money
      })
    },
  }
}

export default product
