import { setToken, getToken, removeToken } from '@/utils/auth';

export default function() {

    const token = getToken();
    const query = {};

    // 开发模式
    if(process.env.NODE_ENV == 'development' && window.localStorage.getItem('devHost')) {
        query.devHost = window.localStorage.getItem('devHost');
    }
    
    if(token) {
        query.authorization = 'Bearer ' + token;
    }
    
    const log = console.log;
    const socket = window.io('/', {
        // 实际使用中可以在这里传递参数
        query,
        // transports: ['websocket']
    });
    
    socket.on('connect', () => {
        const id = socket.id;
        // log('#connect,', id, socket);
        // 监听自身 id 以实现 p2p 通讯
        socket.on(id, msg => {
            log('#receive,', msg);
        });
    });
    
    // 接收在线用户信息
    socket.on('online', msg => {
        log('#online,', msg);
    });
    
    // 系统事件
    socket.on('disconnect', msg => {
        log('#disconnect', msg);
    });
    
    socket.on('disconnecting', () => {
        log('#disconnecting');
    });
    
    socket.on('error', () => {
        log('#error');
    });

    socket.on('serverLog', (msg) => {
        log(msg);
    });
    
    window.socket = socket;

}