import Vue from 'vue'
import router from './router'
import store from './store'
import VueScrollTo from 'vue-scrollto'
import initSocket from '@/utils/socket'
import App from './App.vue'
// import './plugins/element.js'
import * as filters from './filters' // global filters
import VueAnalytics from 'vue-analytics'
import { isDev, jumpToWWW, isMobile, jumpToMobile } from './utils/index';
import './permission'

if(isMobile()) {
  jumpToMobile();
}else {
  if (!jumpToWWW()) {
    init();
  }
}

function init() {

  if(isDev()) {
    let href = window.location.href;
    let host = href.match(/host=\w{0,}/);
    if(host) {
      host = host[0].split('=')[1];
      window.localStorage.setItem('devHost', host)
    }
  }else {
    Vue.use(VueAnalytics, {
      id: 'UA-119925005-4',
      // disableScriptLoader: true, // 必须在html中完成初始化，这里显式禁止去下载ga脚本
      router, // 确保路由切换时可以自动统计
      autoTracking: {
        pageviewOnLoad: false // 当通过网址进来时已经GA在初始化时就发起一次pageview的统计，这里不要重复统计
      }
    })
  }

  initSocket();
  
  Vue.use(VueScrollTo)

  // register global utility filters.
  Object.keys(filters).forEach(key => {
    Vue.filter(key, filters[key])
  })

  Vue.config.productionTip = false

  new Vue({
    router,
    store,
    render: h => h(App),
  }).$mount('#app');
}