import Cookies from 'js-cookie'
import { getAddr } from '@/api/common'

const app = {
  state: {
    loginDialogShow: false,
    registerDialogShow: false,
    scActivityDialogVisible: false,
    addBankCardShow: false,
    areaOptions: [],
    device: 'desktop',
    language: Cookies.get('language') || 'en',
    size: Cookies.get('size') || 'small',
    client: 'desktop-web',
    siteInfo: {},
    currentNavId: 0,
    autoExchange: true,
    hightOddsId: 0,
    isDev: process.env.NODE_ENV == 'development',
  },
  mutations: {
    OPEN_LOGIN_DIALOG: (state) => {
      state.loginDialogShow = true
    },
    CLOSE_LOGIN_DIALOG: (state) => {
      state.loginDialogShow = false
    },
    TOGGLE_LOGIN_DIALOG: (state) => {
      state.loginDialogShow = !state.loginDialogShow;
    },

    OPEN_REGISTER_DIALOG: (state) => {
      state.registerDialogShow = true
    },

    CLOSE_REGISTER_DIALOG: (state) => {
      state.registerDialogShow = false
    },

    TOGGLE_REGISTER_DIALOG: (state) => {
      state.registerDialogShow = !state.registerDialogShow;
    },

    OPEN_SC_ACTIVITY_DIALOG: (state) => {
      state.scActivityDialogVisible = true
    },

    CLOSE_SC_ACTIVITY_DIALOG: (state) => {
      state.scActivityDialogVisible = false
    },

    OPEN_ADD_BANK_CARD_DIALOG: (state) => {
      state.addBankCardShow = true
    },
    CLOSE_ADD_BANK_CARD_DIALOG: (state) => {
      state.addBankCardShow = false
    },
    TOGGLE_ADD_BANK_CARD_DIALOG: (state) => {
      state.addBankCardShow = !state.addBankCardShow;
    },

    SET_AREA_OPTIONS: (state, data) => {
      state.areaOptions = data;
    },

    SET_LANGUAGE: (state, language) => {
      state.language = language
      Cookies.set('language', language)
    },
    SET_SIZE: (state, size) => {
      state.size = size
      Cookies.set('size', size)
    },
    SET_SITE_INFO: (state, data) => {
      state.siteInfo = data
    },
    SET_CURRENT_NAVID: (state, id) => {
      state.currentNavId = id;
    },
  },
  actions: {
    openLoginDialog({ commit }) {
      commit('OPEN_LOGIN_DIALOG');
    },
    closeLoginDialog({ commit }) {
      commit('CLOSE_LOGIN_DIALOG');
    },
    toggleLoginDialog({ commit }) {
      commit('TOGGLE_LOGIN_DIALOG');
    },

    openRegisterDialog({ commit }) {
      commit('OPEN_REGISTER_DIALOG');
    },

    closeRegisterDialog({ commit }) {
      commit('CLOSE_REGISTER_DIALOG');
    },

    openScActivityDialog({ commit }) {
      commit('OPEN_SC_ACTIVITY_DIALOG');
    },

    closeScActivityDialog({ commit }) {
      commit('CLOSE_SC_ACTIVITY_DIALOG');
    },

    toggleRegisterDialog({ commit }) {
      commit('TOGGLE_REGISTER_DIALOG');
    },

    openAddBankCardDialog({ commit, state }) {
      if(state.areaOptions.length == 0) {
          getAddr().then(json => {
            if (json.code == 200) {
                const areaOptions = json.list;
                
                areaOptions.forEach(province => {
                    province.value = province.label = province.name;
                    province.children = province.cities;

                    province.children.forEach(city => {
                        city.value = city.label = city.name;
                    });
                });
                
                commit('SET_AREA_OPTIONS', areaOptions);
            }
          })
      }

      commit('OPEN_ADD_BANK_CARD_DIALOG');
    },
    closeAddBankCardDialog({ commit }) {
      commit('CLOSE_ADD_BANK_CARD_DIALOG');
    },
    toggleAddBankCardDialog({ commit }) {
      commit('TOGGLE_ADD_BANK_CARD_DIALOG');
    },

    setLanguage({ commit }, language) {
      commit('SET_LANGUAGE', language)
    },
    
    setSize({ commit }, size) {
      commit('SET_SIZE', size)
    },

    setSiteInfo({ commit }, data) {
      commit('SET_SITE_INFO', data)
    },

    setCurrentNavId({ commit }, id) {
      commit('SET_CURRENT_NAVID', id)
    },
  }
}

export default app
