<template>
    <span class="money_dropdown">
        <el-dropdown @visible-change="visibleChange" placement="bottom">
            <a href="javascript:;" class="el-dropdown-link gold" :style="{color: color || '#fff'}">
                ￥{{ getTotalMoney() }}<i class="el-icon-arrow-down el-icon--right"></i>
            </a>
            <el-dropdown-menu slot="dropdown" class="money_dropdown_menu">
                <div class="dropdown_money container">
                    <!-- <div class="total">总资产：<span class="m_s">￥{{ getTotalMoney() }}</span></div> -->
                    <el-row :gutter="10">
                        <el-col :span="24">
                            <div class="grid-content bg-purple">
                                <i class="el-icon-loading" v-visible="moneyLoading"></i>
                                <span class="c_label">总资产</span>
                                <span class="m_s">￥{{ getTotalMoney() }}</span>
                            </div>
                        </el-col>
                        <el-col :span="24">
                            <div class="grid-content bg-purple">
                                <i class="el-icon-loading" v-visible="moneyLoading"></i>
                                <span class="c_label">钱包余额</span>
                                <span class="m_s">￥{{ this.user.userInfo.total_money }}</span>

                                <!-- <span class="m_s">￥{{ this.product.balance[0] ? this.product.balance[0].money : 0 }}</span> -->
                            </div>
                        </el-col>
                        <el-col :span="24" :key="item.id" v-for="item in filterProduct">
                            <div class="grid-content bg-purple">
                                <i class="el-icon-loading" v-visible="moneyLoading"></i>
                                <span class="c_label">
                                    <img :src="item.icon | imgFormatter" />
                                    {{ item.name }}
                                </span>
                                <span class="m_s">￥{{ getMoney(item.id) }}</span>
                                <span>
                                    &nbsp;&nbsp;<a href="javascript:;" @click="productReload(item.id)">
                                    <i class="el-icon-refresh"></i>
                                    </a>&nbsp;
                                    <a v-if="getMoney(item.id) > 0" href="javascript:;" @click="returnMoney(item.id)">
                                    回收
                                    </a>
                                </span>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </el-dropdown-menu>
        </el-dropdown>
    </span>
</template>

<script>
import { transfer } from '@/api/user'

export default {
    name: 'Money-Dropdown',
    props: ['user', 'product', 'color'],
    directives: {
        visible: (el, binding) => {
            var value = binding.value;

            if (!!value) {
                el.style.visibility = 'visible';
            } else {
                el.style.visibility = 'hidden';
            }
        }
    },
    data() {
        return {
            userMoney: {
                id: 0,
                name: '钱包余额',
                money: this.user.userInfo.total_money,
            },
            moneyLoading: false,
        }
    },
    computed: {
        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && checkNavIsShow(item.id);
            });
            

            // list.sort((p, n) => {
            //     return this.getMoney(p.id) < this.getMoney(n.id) ? 1 : -1;
            // });

            return list;
        }
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            
        },

        reloadBalance() {
            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
            this.$store.dispatch('productBalance', { product_ids })
        },

        getMoney(id) {
            let money = 0;

            if(id == 0) {
                money = this.user.userInfo.total_money;
            }else {
                for(const key in this.product.balance) {
                    if(key == id) {
                        money = this.product.balance[key].money;
                    }
                }
            }
            
            return Number(money).toFixed(2);
        },
        
        getTotalMoney() {
            let money = Number(this.user.userInfo.total_money);
            
            for(const key in this.product.balance) {
                if(Number(key) !== 0) {
                    money += Number(this.product.balance[key].money);
                }
            }

            // console.log(money);

            // console.log(this.user.userInfo);
            // console.log(this.product.balance);

            return money.toFixed(2);
        },

        productReload(product_id) {
            if(this.moneyLoading) return ;

            this.moneyLoading = true;
            this.$store.dispatch('productBalanceSync', { product_ids: product_id }).then((json) => {
                this.moneyLoading = false;
                if(json.code == 200) {
                    this.$message.success('操作成功，同步余额已受理，请稍后查看金额');
                    setTimeout(() => {
                        this.reloadBalance();
                    }, 1500);
                }else {
                    this.$message.error(json.info);
                }
            });
        },

        returnMoney(product_id) {
            const data = {
                type: 3,
                product_id: product_id,
            }

            this.moneyLoading = true;
            transfer(data).then(json => {

                if (json.code == 200) {
                    this.$message({
                        message: '操作成功，转账已受理，请稍后查看金额',
                        type: 'success',
                        customClass: 'long',
                        duration: 3000,
                        center: true,
                    });

                    setTimeout(() => {
                        this.moneyLoading = false;
                        this.reloadBalance();

                        setTimeout(() => {
                            this.reloadBalance();
                        }, 3000)
                    }, 3000);

                } else {
                    this.moneyLoading = false;
                    this.$message.error(json.info);
                }
            });
        },

        visibleChange(isShow) {
            if(isShow) {
                this.reloadBalance();
            }
        }
    }
}
</script>

